s movePickReverse5{0%{transform:rotate(60deg)}100%{transform:rotate(0deg)}}@keyframes movePickReverse0{0%{transform:rotate(120deg)}100%{transform:rotate(60deg)}}@keyframes movePickReverse1{0%{transform:rotate(180deg)}100%{transform:rotate(120deg)}}@keyframes movePickReverse2{0%{transform:rotate(240deg)}100%{transform:rotate(180deg)}}@keyframes movePickReverse3{0%{transform:rotate(300deg)}100%{transform:rotate(240deg)}}@keyframes movePickReverse4{0%{transform:rotate(360deg)}100%{transform:rotate(300deg)}}
